@import (less) 'color.less';
:host /deep/ *:focus {
  outline: none !important;
  border: 0px;
}

/* Handle stripes;
 */
:host /deep/ .noUi-handle:before {
  font-family: icomoon !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  line-height: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 2.187em;
  background-color: white;
  border-radius: 2.187em;
  margin-left: -8px;
  cursor: pointer;
  color: #258900;
}
:host /deep/ .noUi-handle:after {
  left: 17px;
}

:host /deep/ .noUi-target, .noUi-target * {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-touch-action: none;
  touch-action: none;
  -ms-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:host /deep/ .noUi-target {
  position: relative;
  direction: ltr;
}

:host /deep/ .noUi-base, .noUi-connects {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}


/* Wrapper for all connect elements.
  */

:host /deep/ .noUi-connects {
  overflow: hidden;
  z-index: 0;
}

:host /deep/ .noUi-connect, .noUi-origin {
  will-change: transform;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  -ms-transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}


/* Offset direction
  */

:host /deep/ html:not([dir="rtl"]) .noUi-horizontal .noUi-origin {
  left: auto;
  right: 0;
}


/* Give origins 0 height/width so they don't interfere with clicking the
  * connect elements.
  */

:host /deep/ .noUi-vertical .noUi-origin {
  width: 0;
}

:host /deep/ .noUi-horizontal .noUi-origin {
  height: 0;
}

:host /deep/ .noUi-handle {
  position: absolute;
}

:host /deep/ .noUi-state-tap .noUi-connect, .noUi-state-tap .noUi-origin {
  -webkit-transition: transform 0.3s;
  transition: transform 0.3s;
}

:host /deep/ .noUi-state-drag * {
  cursor: inherit !important;
}


/* Slider size and handle placement;
  */

:host /deep/ .noUi-horizontal {
  height: 18px;
}



:host /deep/ .noUi-horizontal .noUi-handle {
  width: 0px;
  height: 0px;
  left: -0.625em;
  top: 0px;
  margin-left: 100%;
}

:host /deep/ .noUi-vertical {
  width: 18px;
}

:host /deep/ .noUi-vertical .noUi-handle {
  width: 0px;
  height: 0px;
  left: -6px;
  top: -17px;
}

:host /deep/ html:not([dir="rtl"]) .noUi-horizontal .noUi-handle {
  right: -0.625em;
  left: auto;
}


/* Styling;
  * Giving the connect element a border radius causes issues with using transform: scale
  */

:host /deep/ .noUi-target {
  background: #FAFAFA;
  // border-radius: 4px;
  // border: 1px solid #D3D3D3;
  // box-shadow: inset 0 1px 1px #F0F0F0, 0 3px 6px -5px #BBB;
}

:host /deep/ .noUi-connects {
  border-radius: 3px;
}

:host /deep/ .noUi-connect {
  background: #3FB8AF;
}


/* Handles and cursors;
  */

:host /deep/ .noUi-draggable {
  cursor: ew-resize;
}

:host /deep/ .noUi-vertical .noUi-draggable {
  cursor: ns-resize;
}

:host /deep/ .noUi-handle {

  width: 0;
  height: 0;
  left: 0px;
  top: 3px;
  margin-left: 98%;
  cursor: pointer;

  // box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #EBEBEB, 0 3px 6px -3px #BBB;
}

// :host /deep/ .noUi-active {
  // box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #DDD, 0 3px 6px -3px #BBB;
// }


/* Handle stripes;
  */

:host /deep/ .noUi-vertical .noUi-handle:before, .noUi-vertical .noUi-handle:after {
  width: 14px;
  height: 1px;
  left: 6px;
  top: 14px;
}

:host /deep/ .noUi-vertical .noUi-handle:after {
  top: 17px;
}


/* Disabled state;
  */

:host /deep/ [disabled] .noUi-connect {
  background: #B8B8B8;
}

:host /deep/ [disabled].noUi-target, [disabled].noUi-handle, [disabled] .noUi-handle {
  cursor: not-allowed;
}


/* Base;
  *
  */

:host /deep/ .noUi-pips, .noUi-pips * {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:host /deep/ .noUi-pips {
  position: absolute;
  color: #999;
}


/* Values;
  *
  */

:host /deep/ .noUi-value {
  position: absolute;
  white-space: nowrap;
  text-align: center;
}

:host /deep/ .noUi-value-sub {
  color: #ccc;
  font-size: 10px;
}

/* Markings;
  *
  */

:host /deep/ .noUi-marker {
  position: absolute;
  background: #CCC;
}

:host /deep/ .noUi-marker-sub {
  background: #AAA;
}

:host /deep/ .noUi-marker-large {
  background: #AAA;
}


/* Horizontal layout;
  *
*/

:host /deep/ .noUi-pips-horizontal {
  padding: .625em 0;
  height: 5em;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: -1.687em;
  
}

:host /deep/ .noUi-value-horizontal {
  -webkit-transform: translate(-50%, 50%);
  transform: translate(-50%, 50%);
}

:host /deep/ .noUi-rtl .noUi-value-horizontal {
  -webkit-transform: translate(50%, 50%);
  transform: translate(50%, 50%);
}

:host /deep/ .noUi-marker-horizontal.noUi-marker {
  margin-left: -1px;
  width: 0.125em;
  height: 5px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-sub {
  height: 10px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-large {
  height: 15px;
}


/* Vertical layout;
  *
  */

:host /deep/ .noUi-pips-vertical {
  padding: 0 10px;
  height: 100%;
  top: 0;
  left: 100%;
}

:host /deep/ .noUi-value-vertical {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%, 0);
  padding-left: 25px;
}

:host /deep/ .noUi-rtl .noUi-value-vertical {
  -webkit-transform: translate(0, 50%);
  transform: translate(0, 50%);
}

:host /deep/ .noUi-marker-vertical.noUi-marker {
  width: 5px;
  height: 0.125em;
  margin-top: -1px;
}

:host /deep/ .noUi-marker-vertical.noUi-marker-sub {
  width: 10px;
}

:host /deep/ .noUi-marker-vertical.noUi-marker-large {
  width: 15px;
}


:host /deep/ .noUi-horizontal .noUi-tooltip {
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  left: 50%;
  bottom: 120%;
}

:host /deep/ .noUi-vertical .noUi-tooltip {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  top: 50%;
  right: 120%;
}

:host /deep/ .noUi-value-horizontal.noUi-value-large {
  background-color: rgba(111, 111, 111, 1);
  color: transparent;
  // font-size: 0;
  height: 1em;
  width: 1em;
  border-radius: 50%;
  cursor: pointer;
}

:host /deep/ .noUi-value-horizontal.noUi-value-large:hover {
  background-color: @green;
  color: transparent;
  // font-size: 0;
  height: 1em;
  width: 1em;
  border-radius: 50%;
  cursor: pointer;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker {
  margin-left: -1px;
  width: 0.125em;
  height: 0px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-sub {
  height: 0px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-large {
  height: 0px;
}

:host /deep/ .noUi-horizontal {
  height: 0.125em;
  padding: 0px !important;
  top: 0%!important;
  width: 100%!important;
  background-color: rgba(111, 111, 111, 1) !important;
}

:host /deep/ .noUi-tooltip {
  display: block;
  position: absolute;
  // border: 1px solid #D9D9D9;
  // border-radius: 3px;
  // background: #fff;
  // color: #000;
  padding: .375em .812em .375em .812em;
  // text-align: center;
  white-space: nowrap;
  border-width: 0px;
  position: absolute;
  left: 0px;
  // top: 0px;
  width: 4.375em;
  height: 2em;
  background: inherit;
  background-color: rgba(111, 111, 111, 1);
  border: none;
  border-radius: 0px;
  box-shadow: none;
  font-family: 'Arial Bold', 'Arial';
  font-weight: 700;
  font-style: normal;
  font-size: 1em;
  color: #FFFFFF;
  text-align: center;
  line-height: 1.25em;
  margin-left: 0.5em;
  margin-bottom: 1.875em;
  &::after{
    content: "";
    position: absolute;
    top: 100%;
    right: 37%;
    border-top-width: .375em;
    border-right-width: .5em;
    border-bottom-width: .375em;
    border-left-width: .5em;
    border-style: solid;
    border-color: rgba(111, 111, 111, 1) transparent transparent transparent;
  }
}
:host /deep/ .noUi-horizontal .noUi-tooltip {
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  left: 63%;
  bottom: 190%;
}
:host /deep/ .noUi-vertical .noUi-tooltip {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  top: 50%;
  right: 120%;
}

.contrasto-on {
  :host /deep/ .noUi-target, .noUi-target * {
    -webkit-tap-highlight-color: @black !important;
  }
  :host /deep/ .noUi-target {
    background: @black !important;
  }
  :host /deep/ .noUi-connect {
    background: @black !important;
  }
  :host /deep/ .icomoon-noUi-startIcon:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ .icomoon-noUi-endIcon:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ .noUi-handle:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ [disabled] .noUi-connect {
    background: @black;
  }
  :host /deep/ .noUi-pips {
    color: @black;
  }
  :host /deep/ .noUi-value {
    color: transparent
  }
  :host /deep/ .noUi-value-sub {
    color: transparent;
  }
  :host /deep/ .noUi-marker {
    background: @black;
  }
  :host /deep/ .noUi-marker-sub {
    background: @black;
  }
  :host /deep/ .noUi-marker-large {
    background: @black;
  }
  :host /deep/ .noUi-tooltip {
    background-color: @black;
    color: @white;
    &::after{
      border-color: @black transparent transparent transparent;
    }
  }

  :host /deep/ .noUi-value-horizontal.noUi-value-large {
    background-color: @black;;
  }
  
  :host /deep/ .noUi-value-horizontal.noUi-value-large:hover {
    background-color: @white;
    border: 1px solid black;
  }
  

}